export default ()=> {
    $('.date-field').each(function() {
        const field =$(this);
        const input = field.find('input[type="number"]');
        const btnUp = field.find('.date-field__btn.inc');
        const btnDown = field.find('.date-field__btn.dec');
        const min = input.attr('min');
        const max = input.attr('max');
        btnUp.click(function() {
            const oldValue = parseFloat(input.val());
            let newVal = 0;
            if (oldValue >= max) {
              newVal = oldValue;
            } else {
              newVal = oldValue + 1;
            }
            if (newVal < 10) {
                field.find("input").val(`0${newVal}`);
            } else {
                field.find("input").val(newVal);
            }
            field.find("input").trigger("change");
          });
        btnDown.click(function() {
            var oldValue = parseFloat(input.val());
            let newVal = 0;
            if (oldValue <= min) {
                newVal = oldValue;
            } else {
                newVal = oldValue - 1;
            }
            if (newVal < 10) {
                field.find("input").val(`0${newVal}`);
            } else {
                field.find("input").val(newVal);
            }
            field.find("input").trigger("change");
        });
    });
    $('.time-field').each(function() {
        const field =$(this);
        const input = field.find('.time-field__hours');
        const btnUp = field.find('.date-field__btn.inc');
        const btnDown = field.find('.date-field__btn.dec');
        btnUp.click(function() {
            const oldValue = parseFloat(input.val());
            let newVal = 0;
            if (oldValue >= 24) {
              newVal = oldValue;
            } else {
              newVal = oldValue + 1;
            }
            if (newVal < 10) {
                field.find(".time-field__hours").val(`0${newVal}`);
            } else {
                field.find(".time-field__hours").val(newVal);
            }
            field.find(".time-field__hours").trigger("change");
          });
        btnDown.click(function() {
            var oldValue = parseFloat(input.val());
            let newVal = 0;
            if (oldValue <= 0) {
                newVal = oldValue;
            } else {
                newVal = oldValue - 1;
            }
            if (newVal < 10) {
                field.find(".time-field__hours").val(`0${newVal}`);
            } else {
                field.find(".time-field__hours").val(newVal);
            }
            field.find(".time-field__hours").trigger("change");
        });
        field.find('input').each(function() {
            $(this).on('change', ()=>{
                field.find('input[type="hidden"]').val(`${field.find(".time-field__hours").val()}:${field.find(".time-field__minutes").val()}`)
                console.log(field.find('input[type="hidden"]').val())
            })
        })
    });
    $('.time-field__minutes').on('change', function() {
        const field = $(this)
        if (field.val() < 0 || field.val() == '') {
            setTimeout(function() {
                field.val(0)
            }, 500)
        }
        if (field.val() < 10) {
            const val = field.val();
            field.val(`0${val}`);
        }
    })
    
}


