export default () => {
    $('.heading-slider').slick({
        dots: true
    });
    $('.services-item__nav').each(function(){
        $(this).slick({
            slidesToShow: 5,
            slidesToScroll: 1,
            focusOnSelect: true,
            asNavFor: $(this).parent().find('.services-item__slider')
        })
    })
    $('.services-item__slider').slick({})
    $('.service-slider').slick({
        asNavFor: '.service-nav',
        responsive: [
            {
              breakpoint: 767,
              settings: {
                arrows: false
              }
            }
          ]
    });
    $('.service-nav').slick({
        slidesToShow: 7,
        slidesToScroll: 1,
        focusOnSelect: true,
        asNavFor: '.service-slider',
        responsive: [
            {
              breakpoint: 992,
              settings: {
                slidesToShow: 5,
                slidesToScroll: 1
              }
            },
            {
              breakpoint: 767,
              settings: {
                slidesToShow: 2,
                slidesToScroll: 1
              }
            }
          ]
    })
    if ($(window).width() < 768) {
        $('.home-page .products-list').slick({
            dots: true,
            variableWidth: true,
            autoplay: 5000
        });
    }
}


