import $ from "jquery";
import "jquery-mousewheel";
import "malihu-custom-scrollbar-plugin";
import "slick-carousel";
import "./markup-menu";
import Tabs from "./components/tabs";
import sliders from "./components/sliders";
import Select from "./components/select";
import priceSlider from "./components/price-slider";
import inputs from "./components/inputs";
import popups from "./components/pop-up";


global.$ = global.jQuery = $;
require('../../node_modules/air-datepicker/dist/js/datepicker.min.js')
require('../../node_modules/groupinputs/jquery.groupinputs.min.js')
require('./components/accordion');
document.addEventListener("DOMContentLoaded", () => {
    $('.catalog-list, .scrollable').mCustomScrollbar();
    sliders();
    priceSlider();
    inputs();
    popups();
    global.tabs = new Tabs({})
    global.customSelect = new Select();
    $('.catalog-btn').on('click', function(){
        $('.catalog-list').fadeToggle();
    })
    $(document).on('click', function(e) {
        if ($('.catalog').has(e.target).length === 0 && $('.home').has(e.target).length === 0) {
            $('.catalog-list').fadeOut();
        }
    });
    $('.services-block__more').on('click', function(){
        if (!$(this).hasClass('active')) {
            $(this).addClass('active');
            const height = $(this).parent().find('.services-block__caption').height();
            $(this).parent().find('.services-block__content-inner').addClass('active');
            $(this).parent().find('.services-block__content-inner').css('height', height);
        } else {
            $(this).removeClass('active');
            $(this).parent().find('.services-block__content-inner').css('height', 125);
            $(this).parent().find('.services-block__content-inner').removeClass('active');
        }
    });
    $('.filter-handler').on('click', ()=> {
        $('.filter').addClass('active');
    })
    $('.filter-close').on('click', ()=> {
        $('.filter').removeClass('active');
    });
    $('.credit-card__field').groupinputs();
    $.fn.datepicker.language['en'] = {
        days: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
        daysShort: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
        daysMin: ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'],
        months: ['January','February','March','April','May','June', 'July','August','September','October','November','December'],
        monthsShort: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
        today: 'Today',
        clear: 'Clear',
        dateFormat: 'dd.mm.yyyy',
        timeFormat: 'hh:ii aa',
        firstDay: 0
    };
    $('.js-datepicker').datepicker({
        language: 'en',
        navTitles: {
            days: '<span>yyyy</span> MM'
        },
    });
    function formatDate(date) {
        let dd = date.getDate();
        if (dd < 10) dd = '0' + dd;
        let mm = date.getMonth() + 1;
        if (mm < 10) mm = '0' + mm;
        const yyyy = date.getFullYear();
        return dd + '.' + mm + '.' + yyyy;
      }
    $('.js-datepicker').val(formatDate(new Date()))
});



