export default () => {
    $(".modal").on("click", function () {
        const popup = $(this).attr("data-modal");
        $(".pop-up").removeClass("active");
        $(popup).addClass("active");
      });
      $(".pop-up__overlay, .pop-up__close-handler").on("click", function () {
        $(".pop-up").removeClass("active");
      });
}


